import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
// import ProdCuct from '@/components/Product'
// import Experience from '@/components/Experience'
// import About from '@/components/About'
// import Work from '@/components/Work'
import Test from '@/components/Test'

Vue.use(Router)

export default new Router({
  linkActiveClass:"active",
  routes: [
    {
      path: '/',
      title:"主页",
      name: 'Home',
      component: Home
    },
    {
      path: '/Product',
      title:"作品",
      name: 'Product',
	  // component:Product,
      component: ()=>import('@/components/Product'),
      redirect:'/Product/Special',
      children:[
        {
          path:'/Product/Special',
          title:"专题",
          name:'Special',
          // component:Work
		 component:()=>import('@/components/Work')
        },
        {
          path:'/Product/Soft',
          title:"应用软件",
          name:'Soft',
          // component:Work
		  component:()=>import('@/components/Work')
        },
        {
          path:'/Product/Web',
          title:"网站",
          name:'Web',
          // component:Work
		  component:()=>import('@/components/Work')
        },
        {
          path:'/Product/Mobil',
          title:"app应用",
          name:'Mobil',
          // component:Work
		  component:()=>import('@/components/Work')
        }
      ]
    },
    {
      path: '/Experience',
      title:"项目经验",
      name: 'Experience',
      // component: Experience
	  component:()=>import('@/components/Experience')
    },
    {
      path:'/About',
      title:"关于我",
      name:'About',
      // component:About
	  component:()=>import('@/components/About')
    }
  ]
})



