<template>
    <div class="header container">
        <div class="webTitle">
            <a class="logo" href="/">芳草居</a>
            <a href="#" class="burger" :class="{'active':show}" @click="toggleBtn">
                <span></span>
            </a>
            <div class="menuList" v-show="show">
<!--                :class="{'active':nowIndex===index}"-->
               <!-- <ul>
                    <li v-for="(item,index) in menuBar"  @click="nowIndex=index" :class="{'active':nowIndex===index}">
                        <router-link :to="item.url" class="puba" exact>{{item.name}}</router-link>
                    </li>
                </ul>-->
                <ul>
                    <li v-for="(item,index) in menuBar" >
                        <router-link :to="item.url" class="puba" exact>{{item.name}}</router-link>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Header",
        data(){
            return{
                show:true,
                nowIndex:0,
                menuBar:[
                    {
                        name:"主页",
                        url:"/"
                    },
                    {
                        name:"作品",
                        url:"/Product"
                    },
                    {
                        name:"项目经验",
                        url:"/Experience"
                    },
                    {
                        name:"关于我",
                        url:"/About"
                    }
                ]
            }
        },
        methods:{
            toggleBtn(){
                this.show=!this.show;
            }
        },
        computed:{
            getRoute(){
                return this.$router;
            }
        }
    }
</script>

<style scoped>
.header{
margin-bottom: 3.143rem;
}
ul{
    list-style: none;
    margin: 0px;
    padding: 0px;
}
.webTitle{
    margin: 1.429rem auto;
    text-align: left;
}
.webTitle .logo{
    font-size: 1.7rem;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    cursor: pointer;
    line-height: 2.5rem;
    font-weight: bold;
    color:#2c3e50;
    text-decoration: none;
}
.header .menuList{
    background: transparent;
    padding-left: 0.714rem;
    float: right;
}
.header .menuList li{
    display: inline-block;
    padding: 0px 1.429rem;
    line-height: 2.0rem;
    font-size: 1.286rem;
}
.header .menuList li.active{
    border-bottom: 1px #2c3e50 solid;
}
.header .menuList li.active a::after{
    background-color: transparent;
}
/*.header .menuList a{
    display: inline-block;
    padding: 0px 1.429rem;
    line-height: 2.0rem;
    font-size: 1.286rem;
}
.header .menuList a.active{
    border-bottom: 1px #2c3e50 solid;
}
.header .menuList a.active::after{
    background-color: transparent;
}*/
.burger {
    width: 2rem;
    height: 2.286rem;
    cursor: pointer;
    position: relative;
    float: right;
    display: none;
}

.burger:before,
.burger span,
.burger:after {
    width: 100%;
    height: 2px;
    display: block;
    background: #000;
    border-radius: 2px;
    position: absolute;
    opacity: 1;
}

.burger:before,
.burger:after {
    transition: top 0.35s cubic-bezier(0.23, 1, 0.32, 1), opacity 0.35s cubic-bezier(0.23, 1, 0.32, 1), background-color 1.15s cubic-bezier(0.86, 0, 0.07, 1), -webkit-transform 0.35s cubic-bezier(0.23, 1, 0.32, 1);
    transition: top 0.35s cubic-bezier(0.23, 1, 0.32, 1), transform 0.35s cubic-bezier(0.23, 1, 0.32, 1), opacity 0.35s cubic-bezier(0.23, 1, 0.32, 1), background-color 1.15s cubic-bezier(0.86, 0, 0.07, 1);
    transition: top 0.35s cubic-bezier(0.23, 1, 0.32, 1), transform 0.35s cubic-bezier(0.23, 1, 0.32, 1), opacity 0.35s cubic-bezier(0.23, 1, 0.32, 1), background-color 1.15s cubic-bezier(0.86, 0, 0.07, 1), -webkit-transform 0.35s cubic-bezier(0.23, 1, 0.32, 1);
    -webkit-transition: top 0.35s cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 0.35s cubic-bezier(0.23, 1, 0.32, 1), opacity 0.35s cubic-bezier(0.23, 1, 0.32, 1), background-color 1.15s cubic-bezier(0.86, 0, 0.07, 1);
    content: "";
}

.burger:before {
    top: 4px;
}

.burger span {
    top: 15px;
}

.burger:after {
    top: 26px;
}

/* Hover */

.burger:hover:before {
    top: 7px;
}

.burger:hover:after {
    top: 23px;
}

/* Click */

.burger.active span {
    opacity: 0;
}

.burger.active:before,
.burger.active:after {
    top: 40%;
}

.burger.active:before {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=5);
    /*for IE*/
}

.burger.active:after {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=-5);
    /*for IE*/
}

.burger:focus {
    outline: none;
}
@media screen and (min-width:801px){
    .header .menuList li a{
        color:#2c3e50;
        text-decoration: none;
    }
}
@media screen and (max-width:800px){
    .header .menuList{
        background: #2c3e50;
        padding: 0.714rem;
        position: absolute;
        z-index: 999;
        top:50px;
        right: 5px;
        border-radius:5px;
    }
    .header .menuList li{
        display: block;
    }
    .header .menuList li a{
        color:#fff;
        text-decoration: none;

    }
    .burger{
        display: block;
    }
}
</style>



